.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_left{
    display: flex;
}
.header_left > img {
    object-fit: contain;
    height: 60px;
    margin-left: 5px;
}

.header_middle{
    display: flex;
    align-items: center;
    flex: 0.7;
    background-color: whitesmoke;
    padding: 10px;
    border-radius: 5px;
}
.header_middle > .MuiSvgIcon-root{
    color: grey;
}
.header_middle > input {
    border: none;
    width: 100%;
    outline-width: 0;
    padding: 10px;
    background-color: transparent;
    font-size: medium;
}

.header_right {
    display: flex;
    margin-right: 10px;
}