.section{
    display: flex;
    align-items: center;
    min-width: 200px;
    padding: 15px;
    cursor: pointer;
    color: gray;
    border-width: 0 !important;
}
.section-selected{
    background-color: whitesmoke;
    border-width: 3px !important;
}
.section:hover{
    background-color: whitesmoke;
    border-width: 3px !important;
}
.section > h4 {
    font-size: 14px;
    margin-left: 15px;
}